import React from "react";
import myStyle from './BankAccount.module.scss';
import { CopyToClipboard } from "react-copy-to-clipboard";

function Report() {
	const accountGroup1 = [
    { name: "서원석",bank: "00은행", account: "123-456-7890" },
    { name: "안재숙",bank: "00은행", account: "987-654-3210" },
  ];
	const accountGroup2 = [
    { name: "조시호",bank: "00은행", account: "123-456-7890" },
    { name: "김숙자",bank: "00은행", account: "987-654-3210" },
  ];
	const accountNumbers = [
    { name: "서승원 ❤️ 조유하 ",bank: "00은행", account: "123-456-7890" },
  ];

  const handleCopy = (name) => {
    // alert(`${name}님의 계좌번호가 복사되었습니다!`);
  };

  return (
		<div className={myStyle.bankAccount}>
			<div className={myStyle.item1}>
				<div className={myStyle.left}>
					<div className={myStyle.card}>
						<div> 
							{accountGroup1.map((item, index) => (
								<div key={index} className={myStyle.accountItem}>
									<div className={myStyle.title}>
										{item.name} 
										<div className={myStyle.bank}>
											( {item.bank} )
										</div>
									</div>
									<div className={myStyle.account}>{item.account} </div>
									<CopyToClipboard text={item.account} onCopy={() => handleCopy(item.name)}>
										<button className={myStyle.copyButton}>복사하기</button>
									</CopyToClipboard>
									<br/>
								</div>
							))}
						</div>
					</div>
				</div>
				<div className={myStyle.right}>
					<div className={myStyle.card}>
					<div> 
							{accountGroup2.map((item, index) => (
								<div key={index} className={myStyle.accountItem}>
									<div className={myStyle.title}>
										{item.name} 
										<div className={myStyle.bank}>
											( {item.bank} )
										</div>
									</div>
									<div className={myStyle.account}>{item.account} </div>
									<CopyToClipboard text={item.account} onCopy={() => handleCopy(item.name)}>
										<button className={myStyle.copyButton}>복사하기</button>
									</CopyToClipboard>
									<br/>
								</div>
							))}
						</div>
					</div>
				</div>
			</div>

			<div className={myStyle.item2}>
				<div className={myStyle.card}>
				<div> 
					{accountNumbers.map((item, index) => (
						<div key={index} className={myStyle.accountItem}>
							<div className={myStyle.title}>
								{item.name} 
							</div>
							<div className={myStyle.account}> {item.bank} : {item.account} </div>
							<CopyToClipboard text={item.account} onCopy={() => handleCopy(item.name)}>
								<button className={myStyle.copyButton}>복사하기</button>
							</CopyToClipboard>
						</div>
					))}
				</div>
				</div>
			</div>

    </div>
  );
}

export default Report;
